<style lang="less">
@import './less/app';
</style>
<template>
    <div id="app">
        <tabs :items="items" @on-change="onChange"></tabs>
        <router-view></router-view>
    </div>
</template>
<script>
import Tabs from 'components/Tabs'
import Api from './api'

export default {
    components: {
        Tabs
    },
    data() {
        return {
            items: []
        }
    },
    methods: {
        onChange(item, i) {

        }
    },
    created() {
        Api.getCates().then(data => {
            this.items = data
        })
    }

}
</script>



